import * as React from "react"
import { Slot } from "@radix-ui/react-slot"
import { cva } from "class-variance-authority"

import { cn } from "@/lib/utils"

const buttonVariants = cva(
  "inline-flex items-center justify-center gap-2 whitespace-nowrap rounded-lg text-sm font-medium transition-all duration-300 focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 disabled:pointer-events-none disabled:opacity-50 [&_svg]:pointer-events-none [&_svg]:size-4 [&_svg]:shrink-0",
  {
    variants: {
      variant: {
        default: "bg-primary text-primary-foreground hover:bg-primary/90 hover:shadow-gold",
        destructive:
          "bg-destructive text-destructive-foreground hover:bg-destructive/90",
        outline:
          "border border-gold/30 bg-transparent text-foreground hover:bg-gold/10 hover:border-gold/50",
        secondary:
          "bg-secondary text-secondary-foreground hover:bg-secondary/80",
        ghost: "text-foreground hover:bg-muted hover:text-foreground",
        link: "text-primary underline-offset-4 hover:underline",
        gold: "bg-gradient-to-r from-gold-dark via-gold to-gold-dark text-background font-semibold hover:shadow-gold-lg hover:-translate-y-0.5",
        purple: "bg-gradient-to-r from-purple-dark via-purple to-purple-dark text-foreground font-semibold hover:shadow-purple-lg hover:-translate-y-0.5",
        glass: "bg-card/50 backdrop-blur-sm border border-gold/20 text-foreground hover:bg-card/70 hover:border-gold/40",
        premium: "bg-gradient-to-r from-gold via-gold-light to-gold text-background font-bold shadow-gold animate-pulse-gold",
        rarity: {
          common: "bg-rarity-common/20 border border-rarity-common text-rarity-common hover:bg-rarity-common/30",
          uncommon: "bg-rarity-uncommon/20 border border-rarity-uncommon text-rarity-uncommon hover:bg-rarity-uncommon/30",
          rare: "bg-rarity-rare/20 border border-rarity-rare text-rarity-rare hover:bg-rarity-rare/30",
          epic: "bg-rarity-epic/20 border border-rarity-epic text-rarity-epic hover:bg-rarity-epic/30",
          mythic: "bg-rarity-mythic/20 border border-rarity-mythic text-rarity-mythic hover:bg-rarity-mythic/30 animate-pulse-gold",
        },
      },
      size: {
        default: "h-10 px-4 py-2",
        sm: "h-9 rounded-md px-3",
        lg: "h-11 rounded-lg px-8",
        xl: "h-14 rounded-xl px-10 text-base",
        icon: "h-10 w-10",
        "icon-sm": "h-8 w-8",
        "icon-lg": "h-12 w-12",
      },
    },
    defaultVariants: {
      variant: "default",
      size: "default",
    },
  }
)

const Button = React.forwardRef(({ className, variant, size, asChild = false, ...props }, ref) => {
  const Comp = asChild ? Slot : "button"
  return (
    <Comp
      className={cn(buttonVariants({ variant, size, className }))}
      ref={ref}
      {...props}
    />
  )
})
Button.displayName = "Button"

export { Button, buttonVariants }
