// Utility functions
export const formatMoney = (amount) => {
  if (amount >= 1000000000) return `${(amount / 1000000000).toFixed(2)}B`;
  if (amount >= 1000000) return `${(amount / 1000000).toFixed(2)}M`;
  if (amount >= 1000) return `${(amount / 1000).toFixed(2)}K`;
  return `${amount.toFixed(0)}`;
};

export const formatNumber = (num) => {
  if (num >= 1000000000) return `${(num / 1000000000).toFixed(2)}B`;
  if (num >= 1000000) return `${(num / 1000000).toFixed(2)}M`;
  if (num >= 1000) return `${(num / 1000).toFixed(2)}K`;
  return num.toFixed(0);
};

export const getRandomRarity = (boost = 1) => {
  const rand = Math.random() * 100;
  let cumulative = 0;
  
  const rarities = ['mythic', 'epic', 'rare', 'uncommon', 'common'];
  
  for (const rarity of rarities) {
    const chance = RARITY_CONFIG[rarity].chance * (rarity !== 'common' ? boost : 1);
    cumulative += chance;
    if (rand <= cumulative) return rarity;
  }
  return 'common';
};

export const getRandomAuctionItem = (rarity) => {
  const items = AUCTION_ITEMS[rarity];
  return items[Math.floor(Math.random() * items.length)];
};

// Generate multiple auction items for the auction round
export const generateAuctionItems = (count = 8, rareBoost = 1) => {
  const items = [];
  const usedItems = new Set();
  
  for (let i = 0; i < count; i++) {
    const rarity = getRandomRarity(rareBoost);
    let item = getRandomAuctionItem(rarity);
    
    // Avoid duplicates
    let attempts = 0;
    while (usedItems.has(item.id) && attempts < 10) {
      item = getRandomAuctionItem(rarity);
      attempts++;
    }
    
    usedItems.add(item.id);
    
    const config = RARITY_CONFIG[rarity];
    const startingBid = Math.floor(config.minPrice + Math.random() * (config.maxPrice - config.minPrice));
    
    items.push({
      ...item,
      rarity,
      xpReward: config.xp,
      startingBid,
      currentBid: startingBid,
      highestBidder: null,
      bids: []
    });
  }
  
  return items;
};

