import React, { useState } from 'react';
import { GameProvider } from '../context/GameContext';
import { GameHeader } from '../components/game/GameHeader';
import { ClickerSection } from '../components/game/ClickerSection';
import { AuctionSection } from '../components/game/AuctionSection';
import { InvestmentSection } from '../components/game/InvestmentSection';
import { BusinessSection } from '../components/game/BusinessSection';
import { AchievementsSection } from '../components/game/AchievementsSection';
import { InventorySection } from '../components/game/InventorySection';
import { PrestigeSection } from '../components/game/PrestigeSection';
import { PremiumStoreSection } from '../components/game/PremiumStoreSection';
import { LeaderboardSection } from '../components/game/LeaderboardSection';
import { EventNotification } from '../components/game/EventNotification';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '../components/ui/tabs';
import { ScrollArea } from '../components/ui/scroll-area';
import { Sheet, SheetContent, SheetTrigger } from '../components/ui/sheet';
import { 
  MousePointer2, 
  Gavel, 
  BarChart3, 
  Building2, 
  Trophy, 
  Package, 
  RefreshCw, 
  Crown, 
  Users,
  X
} from 'lucide-react';

const GameContent = () => {
  const [activeTab, setActiveTab] = useState('clicker');
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false);
  
  const tabs = [
    { id: 'clicker', label: 'Clicker', icon: MousePointer2 },
    { id: 'auction', label: 'Auction', icon: Gavel },
    { id: 'business', label: 'Business', icon: Building2 },
    { id: 'invest', label: 'Invest', icon: BarChart3 },
    { id: 'achievements', label: 'Achievements', icon: Trophy },
    { id: 'inventory', label: 'Collection', icon: Package },
    { id: 'prestige', label: 'Prestige', icon: RefreshCw },
    { id: 'store', label: 'Store', icon: Crown },
    { id: 'leaderboard', label: 'Leaderboard', icon: Users },
  ];
  
  const renderContent = () => {
    switch (activeTab) {
      case 'clicker':
        return (
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
            <ClickerSection />
            <AuctionSection />
          </div>
        );
      case 'auction':
        return <AuctionSection />;
      case 'business':
        return <BusinessSection />;
      case 'invest':
        return <InvestmentSection />;
      case 'achievements':
        return <AchievementsSection />;
      case 'inventory':
        return <InventorySection />;
      case 'prestige':
        return <PrestigeSection />;
      case 'store':
        return <PremiumStoreSection />;
      case 'leaderboard':
        return <LeaderboardSection />;
      default:
        return <ClickerSection />;
    }
  };
  
  return (
    <div className="min-h-screen bg-background noise-bg">
      <GameHeader onMenuClick={() => setMobileMenuOpen(true)} />
      
      {/* Event Notifications */}
      <EventNotification />
      
      <div className="container mx-auto px-4 py-6">
        <div className="flex gap-6">
          {/* Desktop Sidebar */}
          <aside className="hidden lg:block w-56 flex-shrink-0">
            <div className="sticky top-24">
              <nav className="space-y-1">
                {tabs.map((tab) => {
                  const Icon = tab.icon;
                  return (
                    <button
                      key={tab.id}
                      onClick={() => setActiveTab(tab.id)}
                      className={`
                        w-full flex items-center gap-3 px-4 py-3 rounded-lg text-left transition-all
                        ${activeTab === tab.id 
                          ? 'bg-gold/10 text-gold border border-gold/30' 
                          : 'text-muted-foreground hover:bg-muted hover:text-foreground'
                        }
                      `}
                    >
                      <Icon className="w-5 h-5" />
                      <span className="font-medium">{tab.label}</span>
                    </button>
                  );
                })}
              </nav>
            </div>
          </aside>
          
          {/* Mobile Navigation Sheet */}
          <Sheet open={mobileMenuOpen} onOpenChange={setMobileMenuOpen}>
            <SheetContent side="left" className="w-64 bg-card border-gold/20">
              <div className="flex items-center justify-between mb-6">
                <h2 className="font-display text-lg">Navigation</h2>
              </div>
              <nav className="space-y-1">
                {tabs.map((tab) => {
                  const Icon = tab.icon;
                  return (
                    <button
                      key={tab.id}
                      onClick={() => {
                        setActiveTab(tab.id);
                        setMobileMenuOpen(false);
                      }}
                      className={`
                        w-full flex items-center gap-3 px-4 py-3 rounded-lg text-left transition-all
                        ${activeTab === tab.id 
                          ? 'bg-gold/10 text-gold border border-gold/30' 
                          : 'text-muted-foreground hover:bg-muted hover:text-foreground'
                        }
                      `}
                    >
                      <Icon className="w-5 h-5" />
                      <span className="font-medium">{tab.label}</span>
                    </button>
                  );
                })}
              </nav>
            </SheetContent>
          </Sheet>
          
          {/* Main Content */}
          <main className="flex-1 min-w-0">
            {/* Mobile Tab Bar */}
            <div className="lg:hidden mb-6 overflow-x-auto pb-2">
              <div className="flex gap-2 min-w-max">
                {tabs.slice(0, 5).map((tab) => {
                  const Icon = tab.icon;
                  return (
                    <button
                      key={tab.id}
                      onClick={() => setActiveTab(tab.id)}
                      className={`
                        flex items-center gap-2 px-4 py-2 rounded-lg transition-all whitespace-nowrap
                        ${activeTab === tab.id 
                          ? 'bg-gold/10 text-gold border border-gold/30' 
                          : 'bg-muted/30 text-muted-foreground'
                        }
                      `}
                    >
                      <Icon className="w-4 h-4" />
                      <span className="text-sm">{tab.label}</span>
                    </button>
                  );
                })}
              </div>
            </div>
            
            {/* Page Content */}
            <div className="animate-scale-in">
              {renderContent()}
            </div>
          </main>
        </div>
      </div>
      
      {/* Footer */}
      <footer className="border-t border-border/50 mt-12 py-6">
        <div className="container mx-auto px-4 text-center">
          <p className="text-sm text-muted-foreground">
            Life Tycoon Clicker  Build your empire, one click at a time 
          </p>
          <p className="text-xs text-muted-foreground mt-2">
            Data saved locally in your browser
          </p>
        </div>
      </footer>
    </div>
  );
};

const GamePage = () => {
  return (
    <GameProvider>
      <GameContent />
    </GameProvider>
  );
};

export default GamePage;
