import React, { createContext, useContext, useReducer, useEffect, useCallback } from 'react';
import { 
  BUSINESSES, 
  LIFE_LEVELS, 
  STOCKS, 
  CRYPTOS, 
  ACHIEVEMENTS,
  DAILY_CHALLENGES,
  WEEKLY_CHALLENGES,
  RANDOM_EVENTS,
  RARITY_CONFIG,
  AUCTION_CONFIG,
  generateAuctionItems
} from '../lib/gameData';

const GameContext = createContext(null);

const STORAGE_KEY = 'lifeTycoonSaveData';

const initialState = {
  // Core stats
  money: 100,
  totalMoneyEarned: 0,
  xp: 0,
  totalXpEarned: 0,
  level: 1,
  clicks: 0,
  
  // Prestige
  prestigeCount: 0,
  prestigeBonus: 1,
  
  // Businesses owned (id -> count)
  businesses: {},
  
  // Passive income per second
  passiveIncome: 0,
  
  // Click power
  clickPower: 1,
  
  // Inventory (won auction items)
  inventory: [],
  
  // Auction stats
  auctionsWon: 0,
  rareItemsWon: 0,
  epicItemsWon: 0,
  mythicItemsWon: 0,
  totalSpentOnAuctions: 0,
  
  // Multi-item auction system
  auctionItems: [],
  auctionTimer: 0,
  auctionRoundActive: false,
  selectedAuctionItem: null,
  playerBids: {}, // itemId -> bidAmount
  lastAuctionTime: null,
  nextAuctionTime: null,
  
  // Legacy single auction (kept for compatibility)
  currentAuction: null,
  currentBid: 0,
  playerBid: 0,
  
  // Investments
  stockPortfolio: {},
  cryptoPortfolio: {},
  stockPrices: {},
  cryptoPrices: {},
  totalInvestments: 0,
  investmentProfit: 0,
  
  // Achievements
  unlockedAchievements: [],
  
  // Challenges
  dailyChallengeProgress: {},
  weeklyChallengeProgress: {},
  lastDailyReset: null,
  lastWeeklyReset: null,
  activeDailyChallenges: [],
  activeWeeklyChallenges: [],
  
  // Events
  activeEvent: null,
  eventEndTime: null,
  
  // Cosmetics
  ownedCosmetics: [],
  
  // Premium
  isPremium: false,
  purchasedPremiumItems: [],
  
  // Multipliers
  moneyMultiplier: 1,
  xpMultiplier: 1,
  
  // Daily tracking
  dailyClicks: 0,
  dailyMoney: 0,
  dailyAuctions: 0,
  dailyInvestment: 0,
  dailyUpgrades: 0,
  dailyTrades: 0,
  
  // Settings
  soundEnabled: true,
  notificationsEnabled: true,
  
  // Timestamps
  lastSaveTime: Date.now(),
  lastPlayTime: Date.now()
};

function gameReducer(state, action) {
  switch (action.type) {
    case 'LOAD_GAME':
      return { ...initialState, ...action.payload, lastPlayTime: Date.now() };
      
    case 'CLICK': {
      const currentLevel = LIFE_LEVELS.find(l => l.level === state.level) || LIFE_LEVELS[0];
      const earnedMoney = state.clickPower * currentLevel.clickBonus * state.prestigeBonus * state.moneyMultiplier;
      
      return {
        ...state,
        money: state.money + earnedMoney,
        totalMoneyEarned: state.totalMoneyEarned + earnedMoney,
        clicks: state.clicks + 1,
        dailyClicks: state.dailyClicks + 1,
        dailyMoney: state.dailyMoney + earnedMoney
      };
    }
    
    case 'ADD_PASSIVE_INCOME': {
      const currentLevel = LIFE_LEVELS.find(l => l.level === state.level) || LIFE_LEVELS[0];
      const income = state.passiveIncome * currentLevel.incomeBonus * state.prestigeBonus * state.moneyMultiplier;
      
      return {
        ...state,
        money: state.money + income,
        totalMoneyEarned: state.totalMoneyEarned + income,
        dailyMoney: state.dailyMoney + income
      };
    }
    
    case 'BUY_BUSINESS': {
      const business = BUSINESSES.find(b => b.id === action.payload.businessId);
      if (!business) return state;
      
      const currentCount = state.businesses[business.id] || 0;
      const cost = Math.floor(business.baseCost * Math.pow(business.costMultiplier, currentCount));
      
      if (state.money < cost) return state;
      
      const newPassiveIncome = state.passiveIncome + business.baseIncome;
      const newClickPower = state.clickPower + business.perClick;
      
      return {
        ...state,
        money: state.money - cost,
        businesses: { ...state.businesses, [business.id]: currentCount + 1 },
        passiveIncome: newPassiveIncome,
        clickPower: newClickPower,
        dailyUpgrades: state.dailyUpgrades + 1
      };
    }
    
    case 'START_AUCTION': {
      // Generate 8 auction items
      const items = generateAuctionItems(AUCTION_CONFIG.itemCount, state.activeEvent?.effect?.rareBoost || 1);
      const nextAuction = Date.now() + (AUCTION_CONFIG.intervalMinutes * 60 * 1000);
      
      return {
        ...state,
        auctionItems: items,
        auctionTimer: AUCTION_CONFIG.timerSeconds,
        auctionRoundActive: true,
        playerBids: {},
        selectedAuctionItem: items[0]?.id || null,
        lastAuctionTime: Date.now(),
        nextAuctionTime: nextAuction,
        // Legacy support
        currentAuction: items[0] || null,
        currentBid: items[0]?.currentBid || 0,
        playerBid: 0
      };
    }
    
    case 'PLACE_BID': {
      const { itemId, amount } = action.payload;
      
      if (state.money < amount) return state;
      
      // Find the item
      const itemIndex = state.auctionItems.findIndex(item => item.id === itemId);
      if (itemIndex === -1) return state;
      
      const currentPlayerBid = state.playerBids[itemId] || 0;
      const newPlayerBid = currentPlayerBid + amount;
      
      // Update auction items
      const updatedItems = [...state.auctionItems];
      updatedItems[itemIndex] = {
        ...updatedItems[itemIndex],
        currentBid: Math.max(updatedItems[itemIndex].currentBid, newPlayerBid),
        highestBidder: newPlayerBid >= updatedItems[itemIndex].currentBid ? 'player' : updatedItems[itemIndex].highestBidder
      };
      
      return {
        ...state,
        money: state.money - amount,
        playerBids: { ...state.playerBids, [itemId]: newPlayerBid },
        auctionItems: updatedItems,
        totalSpentOnAuctions: state.totalSpentOnAuctions + amount,
        // Legacy support
        playerBid: itemId === state.selectedAuctionItem ? newPlayerBid : state.playerBid,
        currentBid: itemId === state.selectedAuctionItem ? updatedItems[itemIndex].currentBid : state.currentBid
      };
    }
    
    case 'SELECT_AUCTION_ITEM': {
      const item = state.auctionItems.find(i => i.id === action.payload.itemId);
      return {
        ...state,
        selectedAuctionItem: action.payload.itemId,
        currentAuction: item || null,
        currentBid: item?.currentBid || 0,
        playerBid: state.playerBids[action.payload.itemId] || 0
      };
    }
    
    case 'UPDATE_AUCTION_TIMER': {
      if (state.auctionTimer <= 0 || !state.auctionRoundActive) return state;
      
      // AI competitor bidding on random items
      let updatedItems = [...state.auctionItems];
      
      updatedItems = updatedItems.map(item => {
        const playerBid = state.playerBids[item.id] || 0;
        
        // AI has 20% chance to bid on each item per second
        if (Math.random() < 0.2) {
          const aiIncrement = Math.floor(item.currentBid * 0.05) + Math.floor(Math.random() * 500) + 100;
          if (playerBid < item.currentBid || playerBid === 0) {
            return {
              ...item,
              currentBid: item.currentBid + aiIncrement,
              highestBidder: 'ai'
            };
          }
        }
        return item;
      });
      
      return {
        ...state,
        auctionTimer: state.auctionTimer - 1,
        auctionItems: updatedItems,
        // Update selected item's current bid
        currentBid: updatedItems.find(i => i.id === state.selectedAuctionItem)?.currentBid || state.currentBid
      };
    }
    
    case 'END_AUCTION': {
      if (!state.auctionRoundActive) return state;
      
      let newInventory = [...state.inventory];
      let newAuctionsWon = state.auctionsWon;
      let newDailyAuctions = state.dailyAuctions;
      let newRareItemsWon = state.rareItemsWon;
      let newEpicItemsWon = state.epicItemsWon;
      let newMythicItemsWon = state.mythicItemsWon;
      let totalXpGained = 0;
      
      // Check each item for wins
      state.auctionItems.forEach(item => {
        const playerBid = state.playerBids[item.id] || 0;
        if (playerBid >= item.currentBid && playerBid > 0) {
          // Player won this item
          newInventory.push({ ...item, wonAt: Date.now(), pricePaid: playerBid });
          newAuctionsWon++;
          newDailyAuctions++;
          totalXpGained += item.xpReward * state.xpMultiplier;
          
          if (item.rarity === 'rare') newRareItemsWon++;
          if (item.rarity === 'epic') newEpicItemsWon++;
          if (item.rarity === 'mythic') newMythicItemsWon++;
        }
      });
      
      // Calculate new level
      let newXp = state.xp + totalXpGained;
      let newTotalXp = state.totalXpEarned + totalXpGained;
      let newLevel = state.level;
      
      for (let i = LIFE_LEVELS.length - 1; i >= 0; i--) {
        if (newTotalXp >= LIFE_LEVELS[i].xpRequired && state.totalMoneyEarned >= LIFE_LEVELS[i].moneyRequired) {
          newLevel = LIFE_LEVELS[i].level;
          break;
        }
      }
      
      const nextAuction = Date.now() + (AUCTION_CONFIG.intervalMinutes * 60 * 1000);
      
      return {
        ...state,
        xp: newXp,
        totalXpEarned: newTotalXp,
        level: newLevel,
        inventory: newInventory,
        auctionsWon: newAuctionsWon,
        dailyAuctions: newDailyAuctions,
        rareItemsWon: newRareItemsWon,
        epicItemsWon: newEpicItemsWon,
        mythicItemsWon: newMythicItemsWon,
        auctionItems: [],
        auctionTimer: 0,
        auctionRoundActive: false,
        playerBids: {},
        selectedAuctionItem: null,
        nextAuctionTime: nextAuction,
        currentAuction: null,
        currentBid: 0,
        playerBid: 0
      };
    }
    
    case 'UPDATE_STOCK_PRICES': {
      const newPrices = {};
      STOCKS.forEach(stock => {
        const currentPrice = state.stockPrices[stock.id] || stock.basePrice;
        const volatility = stock.volatility * (state.activeEvent?.effect?.volatilityBoost || 1);
        const change = (Math.random() - 0.5) * 2 * volatility;
        newPrices[stock.id] = Math.max(1, currentPrice * (1 + change));
      });
      return { ...state, stockPrices: newPrices };
    }
    
    case 'UPDATE_CRYPTO_PRICES': {
      const newPrices = {};
      CRYPTOS.forEach(crypto => {
        const currentPrice = state.cryptoPrices[crypto.id] || crypto.basePrice;
        const volatility = crypto.volatility * (state.activeEvent?.effect?.volatilityBoost || 1);
        const change = (Math.random() - 0.5) * 2 * volatility;
        newPrices[crypto.id] = Math.max(0.001, currentPrice * (1 + change));
      });
      return { ...state, cryptoPrices: newPrices };
    }
    
    case 'BUY_STOCK': {
      const { stockId, amount } = action.payload;
      const price = state.stockPrices[stockId];
      const totalCost = price * amount;
      
      if (state.money < totalCost) return state;
      
      const currentHolding = state.stockPortfolio[stockId] || { shares: 0, avgPrice: 0 };
      const newShares = currentHolding.shares + amount;
      const newAvgPrice = (currentHolding.shares * currentHolding.avgPrice + totalCost) / newShares;
      
      return {
        ...state,
        money: state.money - totalCost,
        stockPortfolio: {
          ...state.stockPortfolio,
          [stockId]: { shares: newShares, avgPrice: newAvgPrice }
        },
        totalInvestments: state.totalInvestments + 1,
        dailyInvestment: state.dailyInvestment + totalCost,
        dailyTrades: state.dailyTrades + 1
      };
    }
    
    case 'SELL_STOCK': {
      const { stockId, amount } = action.payload;
      const holding = state.stockPortfolio[stockId];
      
      if (!holding || holding.shares < amount) return state;
      
      const price = state.stockPrices[stockId];
      const totalValue = price * amount;
      const costBasis = holding.avgPrice * amount;
      const profit = totalValue - costBasis;
      
      const newShares = holding.shares - amount;
      
      return {
        ...state,
        money: state.money + totalValue,
        stockPortfolio: {
          ...state.stockPortfolio,
          [stockId]: newShares > 0 ? { ...holding, shares: newShares } : undefined
        },
        investmentProfit: state.investmentProfit + profit,
        dailyTrades: state.dailyTrades + 1
      };
    }
    
    case 'BUY_CRYPTO': {
      const { cryptoId, amount } = action.payload;
      const price = state.cryptoPrices[cryptoId];
      const totalCost = price * amount;
      
      if (state.money < totalCost) return state;
      
      const currentHolding = state.cryptoPortfolio[cryptoId] || { amount: 0, avgPrice: 0 };
      const newAmount = currentHolding.amount + amount;
      const newAvgPrice = (currentHolding.amount * currentHolding.avgPrice + totalCost) / newAmount;
      
      return {
        ...state,
        money: state.money - totalCost,
        cryptoPortfolio: {
          ...state.cryptoPortfolio,
          [cryptoId]: { amount: newAmount, avgPrice: newAvgPrice }
        },
        totalInvestments: state.totalInvestments + 1,
        dailyInvestment: state.dailyInvestment + totalCost,
        dailyTrades: state.dailyTrades + 1
      };
    }
    
    case 'SELL_CRYPTO': {
      const { cryptoId, amount } = action.payload;
      const holding = state.cryptoPortfolio[cryptoId];
      
      if (!holding || holding.amount < amount) return state;
      
      const price = state.cryptoPrices[cryptoId];
      const totalValue = price * amount;
      const costBasis = holding.avgPrice * amount;
      const profit = totalValue - costBasis;
      
      const newAmount = holding.amount - amount;
      
      return {
        ...state,
        money: state.money + totalValue,
        cryptoPortfolio: {
          ...state.cryptoPortfolio,
          [cryptoId]: newAmount > 0 ? { ...holding, amount: newAmount } : undefined
        },
        investmentProfit: state.investmentProfit + profit,
        dailyTrades: state.dailyTrades + 1
      };
    }
    
    case 'UNLOCK_ACHIEVEMENT': {
      if (state.unlockedAchievements.includes(action.payload.achievementId)) return state;
      
      const achievement = ACHIEVEMENTS.find(a => a.id === action.payload.achievementId);
      if (!achievement) return state;
      
      return {
        ...state,
        unlockedAchievements: [...state.unlockedAchievements, achievement.id],
        money: state.money + (achievement.reward.money || 0),
        xp: state.xp + (achievement.reward.xp || 0),
        totalXpEarned: state.totalXpEarned + (achievement.reward.xp || 0)
      };
    }
    
    case 'TRIGGER_EVENT': {
      const event = RANDOM_EVENTS[Math.floor(Math.random() * RANDOM_EVENTS.length)];
      let newState = { ...state, activeEvent: event };
      
      // Apply immediate effects
      if (event.effect.xp) {
        newState.xp += event.effect.xp;
        newState.totalXpEarned += event.effect.xp;
      }
      if (event.effect.money) {
        newState.money += event.effect.money;
        newState.totalMoneyEarned += event.effect.money;
      }
      if (event.effect.portfolioLoss) {
        // Calculate portfolio value and reduce
        let portfolioValue = 0;
        Object.entries(state.stockPortfolio).forEach(([id, holding]) => {
          if (holding) portfolioValue += holding.shares * (state.stockPrices[id] || 0);
        });
        Object.entries(state.cryptoPortfolio).forEach(([id, holding]) => {
          if (holding) portfolioValue += holding.amount * (state.cryptoPrices[id] || 0);
        });
        // For simplicity, just show the effect in UI
      }
      if (event.effect.moneyLoss) {
        const loss = state.money * event.effect.moneyLoss;
        newState.money = Math.max(0, state.money - loss);
      }
      
      if (event.effect.duration) {
        newState.eventEndTime = Date.now() + event.effect.duration * 1000;
      } else {
        newState.eventEndTime = Date.now() + 5000; // 5 second display
      }
      
      return newState;
    }
    
    case 'CLEAR_EVENT':
      return { ...state, activeEvent: null, eventEndTime: null };
    
    case 'PRESTIGE': {
      if (state.level < 8) return state; // Must be Mogul level
      
      const newPrestigeBonus = state.prestigeBonus + 0.1;
      
      return {
        ...initialState,
        prestigeCount: state.prestigeCount + 1,
        prestigeBonus: newPrestigeBonus,
        unlockedAchievements: state.unlockedAchievements,
        ownedCosmetics: state.ownedCosmetics,
        isPremium: state.isPremium,
        purchasedPremiumItems: state.purchasedPremiumItems,
        moneyMultiplier: state.moneyMultiplier,
        xpMultiplier: state.xpMultiplier,
        soundEnabled: state.soundEnabled,
        notificationsEnabled: state.notificationsEnabled
      };
    }
    
    case 'BUY_COSMETIC': {
      const { cosmeticId, xpCost } = action.payload;
      
      if (state.xp < xpCost || state.ownedCosmetics.includes(cosmeticId)) return state;
      
      return {
        ...state,
        xp: state.xp - xpCost,
        ownedCosmetics: [...state.ownedCosmetics, cosmeticId]
      };
    }
    
    case 'PURCHASE_PREMIUM': {
      const { itemId } = action.payload;
      
      return {
        ...state,
        purchasedPremiumItems: [...state.purchasedPremiumItems, itemId],
        isPremium: itemId === 'pm5' ? true : state.isPremium,
        moneyMultiplier: itemId === 'pm1' || itemId === 'pm5' ? state.moneyMultiplier * 2 : state.moneyMultiplier,
        xpMultiplier: itemId === 'pm2' || itemId === 'pm5' ? state.xpMultiplier * 1.5 : state.xpMultiplier
      };
    }
    
    case 'RESET_DAILY_CHALLENGES': {
      // Select 3 random daily challenges
      const shuffled = [...DAILY_CHALLENGES].sort(() => Math.random() - 0.5);
      const selected = shuffled.slice(0, 3);
      
      return {
        ...state,
        activeDailyChallenges: selected.map(c => c.id),
        dailyChallengeProgress: {},
        dailyClicks: 0,
        dailyMoney: 0,
        dailyAuctions: 0,
        dailyInvestment: 0,
        dailyUpgrades: 0,
        dailyTrades: 0,
        lastDailyReset: Date.now()
      };
    }
    
    case 'RESET_WEEKLY_CHALLENGES': {
      const shuffled = [...WEEKLY_CHALLENGES].sort(() => Math.random() - 0.5);
      const selected = shuffled.slice(0, 2);
      
      return {
        ...state,
        activeWeeklyChallenges: selected.map(c => c.id),
        weeklyChallengeProgress: {},
        lastWeeklyReset: Date.now()
      };
    }
    
    case 'COMPLETE_CHALLENGE': {
      const { challengeId, isDaily } = action.payload;
      const challenges = isDaily ? DAILY_CHALLENGES : WEEKLY_CHALLENGES;
      const challenge = challenges.find(c => c.id === challengeId);
      
      if (!challenge) return state;
      
      const progressKey = isDaily ? 'dailyChallengeProgress' : 'weeklyChallengeProgress';
      
      if (state[progressKey][challengeId]) return state; // Already completed
      
      return {
        ...state,
        [progressKey]: { ...state[progressKey], [challengeId]: true },
        money: state.money + challenge.reward.money,
        xp: state.xp + challenge.reward.xp,
        totalXpEarned: state.totalXpEarned + challenge.reward.xp
      };
    }
    
    case 'UPDATE_LEVEL': {
      let newLevel = state.level;
      
      for (let i = LIFE_LEVELS.length - 1; i >= 0; i--) {
        if (state.totalXpEarned >= LIFE_LEVELS[i].xpRequired && 
            state.totalMoneyEarned >= LIFE_LEVELS[i].moneyRequired) {
          newLevel = LIFE_LEVELS[i].level;
          break;
        }
      }
      
      return { ...state, level: newLevel };
    }
    
    case 'SAVE_GAME':
      return { ...state, lastSaveTime: Date.now() };
      
    default:
      return state;
  }
}

export function GameProvider({ children }) {
  const [state, dispatch] = useReducer(gameReducer, initialState);
  
  // Load game on mount
  useEffect(() => {
    const savedData = localStorage.getItem(STORAGE_KEY);
    if (savedData) {
      try {
        const parsed = JSON.parse(savedData);
        dispatch({ type: 'LOAD_GAME', payload: parsed });
      } catch (e) {
        console.error('Failed to load save data:', e);
      }
    }
    
    // Initialize prices
    dispatch({ type: 'UPDATE_STOCK_PRICES' });
    dispatch({ type: 'UPDATE_CRYPTO_PRICES' });
    
    // Initialize daily/weekly challenges if needed
    dispatch({ type: 'RESET_DAILY_CHALLENGES' });
    dispatch({ type: 'RESET_WEEKLY_CHALLENGES' });
  }, []);
  
  // Auto-save
  useEffect(() => {
    const saveInterval = setInterval(() => {
      localStorage.setItem(STORAGE_KEY, JSON.stringify(state));
      dispatch({ type: 'SAVE_GAME' });
    }, 30000); // Save every 30 seconds
    
    return () => clearInterval(saveInterval);
  }, [state]);
  
  // Save on unload
  useEffect(() => {
    const handleUnload = () => {
      localStorage.setItem(STORAGE_KEY, JSON.stringify(state));
    };
    window.addEventListener('beforeunload', handleUnload);
    return () => window.removeEventListener('beforeunload', handleUnload);
  }, [state]);
  
  // Passive income tick
  useEffect(() => {
    const incomeInterval = setInterval(() => {
      if (state.passiveIncome > 0) {
        dispatch({ type: 'ADD_PASSIVE_INCOME' });
      }
    }, 1000);
    
    return () => clearInterval(incomeInterval);
  }, [state.passiveIncome]);
  
  // Price updates
  useEffect(() => {
    const priceInterval = setInterval(() => {
      dispatch({ type: 'UPDATE_STOCK_PRICES' });
      dispatch({ type: 'UPDATE_CRYPTO_PRICES' });
    }, 5000);
    
    return () => clearInterval(priceInterval);
  }, []);
  
  // Auction timer
  useEffect(() => {
    if (state.auctionTimer > 0 && state.auctionRoundActive) {
      const timer = setInterval(() => {
        dispatch({ type: 'UPDATE_AUCTION_TIMER' });
      }, 1000);
      return () => clearInterval(timer);
    } else if (state.auctionRoundActive && state.auctionTimer === 0) {
      dispatch({ type: 'END_AUCTION' });
    }
  }, [state.auctionTimer, state.auctionRoundActive]);
  
  // Random events (every 2-5 minutes)
  useEffect(() => {
    const eventInterval = setInterval(() => {
      if (Math.random() < 0.3 && !state.activeEvent) {
        dispatch({ type: 'TRIGGER_EVENT' });
      }
    }, 120000);
    
    return () => clearInterval(eventInterval);
  }, [state.activeEvent]);
  
  // Clear expired events
  useEffect(() => {
    if (state.eventEndTime && Date.now() > state.eventEndTime) {
      dispatch({ type: 'CLEAR_EVENT' });
    }
  }, [state.eventEndTime]);
  
  // Check achievements
  useEffect(() => {
    ACHIEVEMENTS.forEach(achievement => {
      if (state.unlockedAchievements.includes(achievement.id)) return;
      
      let progress = 0;
      switch (achievement.type) {
        case 'money': progress = state.totalMoneyEarned; break;
        case 'clicks': progress = state.clicks; break;
        case 'auctions': progress = state.auctionsWon; break;
        case 'rareItems': progress = state.rareItemsWon; break;
        case 'epicItems': progress = state.epicItemsWon; break;
        case 'mythicItems': progress = state.mythicItemsWon; break;
        case 'investments': progress = state.totalInvestments; break;
        case 'businesses': progress = Object.values(state.businesses).filter(v => v > 0).length; break;
        case 'allBusinesses': progress = Object.keys(state.businesses).length; break;
        case 'prestiges': progress = state.prestigeCount; break;
        default: break;
      }
      
      if (progress >= achievement.target) {
        dispatch({ type: 'UNLOCK_ACHIEVEMENT', payload: { achievementId: achievement.id } });
      }
    });
  }, [state]);
  
  // Level updates
  useEffect(() => {
    dispatch({ type: 'UPDATE_LEVEL' });
  }, [state.totalXpEarned, state.totalMoneyEarned]);
  
  const click = useCallback(() => dispatch({ type: 'CLICK' }), []);
  const buyBusiness = useCallback((businessId) => dispatch({ type: 'BUY_BUSINESS', payload: { businessId } }), []);
  const startAuction = useCallback(() => dispatch({ type: 'START_AUCTION' }), []);
  const placeBid = useCallback((itemId, amount) => dispatch({ type: 'PLACE_BID', payload: { itemId, amount } }), []);
  const selectAuctionItem = useCallback((itemId) => dispatch({ type: 'SELECT_AUCTION_ITEM', payload: { itemId } }), []);
  const buyStock = useCallback((stockId, amount) => dispatch({ type: 'BUY_STOCK', payload: { stockId, amount } }), []);
  const sellStock = useCallback((stockId, amount) => dispatch({ type: 'SELL_STOCK', payload: { stockId, amount } }), []);
  const buyCrypto = useCallback((cryptoId, amount) => dispatch({ type: 'BUY_CRYPTO', payload: { cryptoId, amount } }), []);
  const sellCrypto = useCallback((cryptoId, amount) => dispatch({ type: 'SELL_CRYPTO', payload: { cryptoId, amount } }), []);
  const prestige = useCallback(() => dispatch({ type: 'PRESTIGE' }), []);
  const buyCosmetic = useCallback((cosmeticId, xpCost) => dispatch({ type: 'BUY_COSMETIC', payload: { cosmeticId, xpCost } }), []);
  const purchasePremium = useCallback((itemId) => dispatch({ type: 'PURCHASE_PREMIUM', payload: { itemId } }), []);
  const triggerEvent = useCallback(() => dispatch({ type: 'TRIGGER_EVENT' }), []);
  const completeChallenge = useCallback((challengeId, isDaily) => dispatch({ type: 'COMPLETE_CHALLENGE', payload: { challengeId, isDaily } }), []);
  
  const value = {
    state,
    actions: {
      click,
      buyBusiness,
      startAuction,
      placeBid,
      selectAuctionItem,
      buyStock,
      sellStock,
      buyCrypto,
      sellCrypto,
      prestige,
      buyCosmetic,
      purchasePremium,
      triggerEvent,
      completeChallenge
    }
  };
  
  return (
    <GameContext.Provider value={value}>
      {children}
    </GameContext.Provider>
  );
}

export function useGame() {
  const context = useContext(GameContext);
  if (!context) {
    throw new Error('useGame must be used within a GameProvider');
  }
  return context;
}
