import React from 'react';
import { useGame } from '../../context/GameContext';
import { formatMoney, LIFE_LEVELS } from '../../lib/gameData';
import { Badge } from '../ui/badge';
import { 
  Coins, 
  Sparkles, 
  TrendingUp, 
  Menu, 
  Crown,
  BookOpen,
  GraduationCap,
  Briefcase,
  LineChart,
  Rocket,
  Gem
} from 'lucide-react';

// Icon mapping for life levels
const LevelIcon = ({ iconName, className }) => {
  const icons = {
    BookOpen: BookOpen,
    GraduationCap: GraduationCap,
    Briefcase: Briefcase,
    LineChart: LineChart,
    Rocket: Rocket,
    TrendingUp: TrendingUp,
    Gem: Gem,
    Crown: Crown
  };
  
  const Icon = icons[iconName] || BookOpen;
  return <Icon className={className} />;
};

export const GameHeader = ({ onMenuClick }) => {
  const { state } = useGame();
  
  const currentLevel = LIFE_LEVELS.find(l => l.level === state.level) || LIFE_LEVELS[0];
  
  return (
    <header className="sticky top-0 z-40 w-full border-b border-gold/20 bg-background/80 backdrop-blur-md">
      <div className="container mx-auto px-4">
        <div className="flex items-center justify-between h-16">
          {/* Logo & Title */}
          <div className="flex items-center gap-3">
            <button 
              onClick={onMenuClick}
              className="lg:hidden p-2 hover:bg-muted rounded-lg transition-colors"
            >
              <Menu className="w-5 h-5" />
            </button>
            <div className="flex items-center gap-2">
              <div className="w-9 h-9 rounded-lg bg-gold/10 border border-gold/20 flex items-center justify-center">
                <Gem className="w-5 h-5 text-gold" />
              </div>
              <div className="hidden sm:block">
                <h1 className="font-display text-lg md:text-xl gradient-text-gold">Life Tycoon</h1>
                <p className="text-xs text-muted-foreground">Clicker Edition</p>
              </div>
            </div>
          </div>
          
          {/* Stats Bar */}
          <div className="flex items-center gap-2 md:gap-4">
            {/* Premium Badge */}
            {state.isPremium && (
              <Badge className="hidden sm:flex premium-badge">
                <Crown className="w-3 h-3 mr-1" />
                Premium
              </Badge>
            )}
            
            {/* Level */}
            <div className="hidden md:flex items-center gap-2 px-3 py-1.5 bg-muted/50 rounded-lg border border-border/50">
              <div className="w-6 h-6 rounded bg-gold/10 flex items-center justify-center">
                <LevelIcon iconName={currentLevel.iconName} className="w-4 h-4 text-gold" />
              </div>
              <div>
                <p className="text-xs text-muted-foreground leading-none">Level</p>
                <p className="text-sm font-semibold">{state.level}</p>
              </div>
            </div>
            
            {/* Money */}
            <div className="flex items-center gap-2 px-3 py-1.5 bg-gold/10 rounded-lg border border-gold/30">
              <Coins className="w-4 h-4 text-gold" />
              <span className="font-mono text-sm md:text-base text-gold font-semibold number-increase">
                {formatMoney(state.money)}
              </span>
            </div>
            
            {/* XP */}
            <div className="hidden sm:flex items-center gap-2 px-3 py-1.5 bg-purple/10 rounded-lg border border-purple/30">
              <Sparkles className="w-4 h-4 text-purple" />
              <span className="font-mono text-sm text-purple font-semibold">
                {state.xp.toFixed(0)} XP
              </span>
            </div>
            
            {/* Passive Income */}
            <div className="hidden lg:flex items-center gap-2 px-3 py-1.5 bg-muted/50 rounded-lg border border-border/50">
              <TrendingUp className="w-4 h-4 text-success" />
              <span className="font-mono text-sm text-success">
                {formatMoney(state.passiveIncome * (currentLevel?.incomeBonus || 1) * state.prestigeBonus * state.moneyMultiplier)}/s
              </span>
            </div>
          </div>
        </div>
      </div>
    </header>
  );
};

export default GameHeader;
