import React from 'react';
import { useGame } from '../../context/GameContext';
import { Card, CardContent, CardHeader, CardTitle } from '../ui/card';
import { Button } from '../ui/button';
import { Badge } from '../ui/badge';
import { ScrollArea } from '../ui/scroll-area';
import { Progress } from '../ui/progress';
import { LIFE_LEVELS, formatMoney } from '../../lib/gameData';
import { 
  RefreshCw, 
  Crown, 
  Sparkles, 
  TrendingUp, 
  AlertTriangle, 
  Check,
  BookOpen,
  GraduationCap,
  Briefcase,
  LineChart,
  Rocket,
  Gem
} from 'lucide-react';
import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
  AlertDialogTrigger,
} from '../ui/alert-dialog';

// Icon mapping for life levels
const LevelIcon = ({ iconName, className }) => {
  const icons = {
    BookOpen: BookOpen,
    GraduationCap: GraduationCap,
    Briefcase: Briefcase,
    LineChart: LineChart,
    Rocket: Rocket,
    TrendingUp: TrendingUp,
    Gem: Gem,
    Crown: Crown
  };
  
  const Icon = icons[iconName] || BookOpen;
  return <Icon className={className} />;
};

export const PrestigeSection = () => {
  const { state, actions } = useGame();
  
  const currentLevel = LIFE_LEVELS.find(l => l.level === state.level) || LIFE_LEVELS[0];
  const isMogul = state.level >= 8;
  const nextPrestigeBonus = state.prestigeBonus + 0.1;
  
  // Calculate progress to Mogul
  const mogulLevel = LIFE_LEVELS.find(l => l.level === 8);
  const xpProgress = mogulLevel 
    ? Math.min((state.totalXpEarned / mogulLevel.xpRequired) * 100, 100)
    : 100;
  const moneyProgress = mogulLevel 
    ? Math.min((state.totalMoneyEarned / mogulLevel.moneyRequired) * 100, 100)
    : 100;
  
  return (
    <Card variant="glass" className="relative overflow-hidden">
      <div className="absolute inset-0 bg-gradient-to-br from-purple/10 via-transparent to-gold/10 pointer-events-none" />
      
      <CardHeader className="pb-3">
        <CardTitle className="flex items-center gap-2 text-xl">
          <RefreshCw className="w-5 h-5 text-purple" />
          Prestige System
        </CardTitle>
      </CardHeader>
      
      <CardContent className="space-y-4">
        {/* Current Status */}
        <div className="p-4 bg-muted/30 rounded-xl border border-purple/20">
          <div className="flex items-center justify-between mb-3">
            <div className="flex items-center gap-3">
              <div className="w-12 h-12 rounded-xl bg-gold/10 border border-gold/20 flex items-center justify-center">
                <LevelIcon iconName={currentLevel.iconName} className="w-6 h-6 text-gold" />
              </div>
              <div>
                <p className="font-display text-lg">{currentLevel.name}</p>
                <p className="text-xs text-muted-foreground">Level {state.level}</p>
              </div>
            </div>
            <Badge variant="outline" className="text-purple border-purple/30">
              <Sparkles className="w-3 h-3 mr-1" />
              {state.prestigeCount} Rebirths
            </Badge>
          </div>
          
          <div className="grid grid-cols-2 gap-3 text-sm">
            <div className="p-2 bg-card/50 rounded-lg">
              <p className="text-muted-foreground text-xs">Current Bonus</p>
              <p className="font-mono text-gold text-lg">x{state.prestigeBonus.toFixed(1)}</p>
            </div>
            <div className="p-2 bg-card/50 rounded-lg">
              <p className="text-muted-foreground text-xs">Next Bonus</p>
              <p className="font-mono text-purple text-lg">x{nextPrestigeBonus.toFixed(1)}</p>
            </div>
          </div>
        </div>
        
        {/* Progress to Mogul */}
        {!isMogul && (
          <div className="space-y-3">
            <h4 className="font-semibold text-sm flex items-center gap-2">
              <Crown className="w-4 h-4 text-gold" />
              Progress to Mogul (Prestige Unlock)
            </h4>
            
            <div className="p-3 bg-muted/20 rounded-lg space-y-3">
              <div>
                <div className="flex justify-between text-xs text-muted-foreground mb-1">
                  <span>XP Required</span>
                  <span>{state.totalXpEarned.toFixed(0)} / {mogulLevel?.xpRequired}</span>
                </div>
                <Progress value={xpProgress} className="h-2" />
              </div>
              
              <div>
                <div className="flex justify-between text-xs text-muted-foreground mb-1">
                  <span>Money Required</span>
                  <span>{formatMoney(state.totalMoneyEarned)} / {formatMoney(mogulLevel?.moneyRequired || 0)}</span>
                </div>
                <Progress value={moneyProgress} className="h-2" />
              </div>
            </div>
          </div>
        )}
        
        {/* Prestige Info */}
        <div className="p-4 bg-card/50 rounded-xl border border-border/50">
          <h4 className="font-semibold text-sm mb-2 flex items-center gap-2">
            <TrendingUp className="w-4 h-4 text-gold" />
            Prestige Benefits
          </h4>
          <ul className="text-xs text-muted-foreground space-y-1.5">
            <li className="flex items-center gap-2">
              <Check className="w-3 h-3 text-success" /> +10% permanent bonus to all earnings
            </li>
            <li className="flex items-center gap-2">
              <Check className="w-3 h-3 text-success" /> Keep all achievements unlocked
            </li>
            <li className="flex items-center gap-2">
              <Check className="w-3 h-3 text-success" /> Keep all cosmetic items
            </li>
            <li className="flex items-center gap-2">
              <Check className="w-3 h-3 text-success" /> Keep premium purchases
            </li>
            <li className="flex items-center gap-2">
              <Check className="w-3 h-3 text-success" /> Start fresh with enhanced power
            </li>
          </ul>
        </div>
        
        {/* What You Lose */}
        <div className="p-4 bg-destructive/10 rounded-xl border border-destructive/20">
          <h4 className="font-semibold text-sm mb-2 flex items-center gap-2 text-destructive">
            <AlertTriangle className="w-4 h-4" />
            What You'll Lose
          </h4>
          <ul className="text-xs text-muted-foreground space-y-1">
            <li> All money: {formatMoney(state.money)}</li>
            <li> All XP progress</li>
            <li> All businesses owned</li>
            <li> All investment portfolio</li>
            <li> All inventory items</li>
            <li> Life level resets to Student</li>
          </ul>
        </div>
        
        {/* Prestige Button */}
        <AlertDialog>
          <AlertDialogTrigger asChild>
            <Button 
              variant={isMogul ? "premium" : "outline"}
              size="lg"
              disabled={!isMogul}
              className="w-full gap-2"
            >
              <RefreshCw className="w-5 h-5" />
              {isMogul ? 'Prestige Now!' : 'Reach Mogul Level to Prestige'}
            </Button>
          </AlertDialogTrigger>
          <AlertDialogContent className="bg-card border-gold/30">
            <AlertDialogHeader>
              <AlertDialogTitle className="flex items-center gap-2">
                <RefreshCw className="w-5 h-5 text-purple" />
                Confirm Prestige
              </AlertDialogTitle>
              <AlertDialogDescription>
                Are you sure you want to prestige? You will lose all your current progress 
                but gain a permanent {((nextPrestigeBonus - state.prestigeBonus) * 100).toFixed(0)}% bonus 
                to all earnings. This cannot be undone!
              </AlertDialogDescription>
            </AlertDialogHeader>
            <AlertDialogFooter>
              <AlertDialogCancel>Cancel</AlertDialogCancel>
              <AlertDialogAction 
                onClick={actions.prestige}
                className="bg-gradient-to-r from-purple-dark via-purple to-purple-dark text-foreground"
              >
                Prestige
              </AlertDialogAction>
            </AlertDialogFooter>
          </AlertDialogContent>
        </AlertDialog>
        
        {/* Life Levels Reference */}
        <div className="mt-4">
          <h4 className="font-semibold text-sm mb-3">Life Stages</h4>
          <ScrollArea className="h-[200px] pr-4">
            <div className="space-y-2">
              {LIFE_LEVELS.map((level) => (
                <div 
                  key={level.level}
                  className={`
                    p-3 rounded-xl border transition-all flex items-center gap-3
                    ${state.level === level.level 
                      ? 'bg-gold/10 border-gold/30' 
                      : state.level > level.level 
                        ? 'bg-success/10 border-success/30' 
                        : 'bg-muted/30 border-border/50'
                    }
                  `}
                >
                  <div className={`
                    w-10 h-10 rounded-lg flex items-center justify-center
                    ${state.level >= level.level ? 'bg-gold/20' : 'bg-muted/50'}
                  `}>
                    <LevelIcon 
                      iconName={level.iconName} 
                      className={`w-5 h-5 ${state.level >= level.level ? 'text-gold' : 'text-muted-foreground'}`} 
                    />
                  </div>
                  <div className="flex-1">
                    <p className="font-semibold text-sm">{level.name}</p>
                    <p className="text-xs text-muted-foreground">
                      {level.xpRequired} XP  {formatMoney(level.moneyRequired)}
                    </p>
                  </div>
                  <Badge variant="outline" className="text-xs">
                    x{level.clickBonus}
                  </Badge>
                </div>
              ))}
            </div>
          </ScrollArea>
        </div>
      </CardContent>
    </Card>
  );
};

export default PrestigeSection;
