import React from 'react';
import { useGame } from '../../context/GameContext';
import { Card, CardContent, CardHeader, CardTitle } from '../ui/card';
import { Button } from '../ui/button';
import { Badge } from '../ui/badge';
import { ScrollArea } from '../ui/scroll-area';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '../ui/tabs';
import { PREMIUM_ITEMS, COSMETIC_ITEMS, formatMoney } from '../../lib/gameData';
import { Crown, Palette, ShoppingBag, Check, Sparkles, Star } from 'lucide-react';
import { toast } from 'sonner';

export const PremiumStoreSection = () => {
  const { state, actions } = useGame();
  
  const handlePurchasePremium = (item) => {
    // Mock purchase - in real app would integrate payment
    actions.purchasePremium(item.id);
    toast.success(`Purchased ${item.name}!`, {
      description: 'Thank you for your purchase! (Mock)',
    });
  };
  
  const handleBuyCosmetic = (cosmetic) => {
    if (state.xp >= cosmetic.xpCost) {
      actions.buyCosmetic(cosmetic.id, cosmetic.xpCost);
      toast.success(`Unlocked ${cosmetic.name}!`);
    } else {
      toast.error('Not enough XP!');
    }
  };
  
  return (
    <Card variant="glass" className="relative overflow-hidden">
      <div className="absolute inset-0 bg-gradient-to-br from-gold/10 via-transparent to-purple/10 pointer-events-none" />
      
      <CardHeader className="pb-3">
        <div className="flex items-center justify-between">
          <CardTitle className="flex items-center gap-2 text-xl">
            <ShoppingBag className="w-5 h-5 text-gold" />
            Premium Store
          </CardTitle>
          {state.isPremium && (
            <Badge className="premium-badge">
              <Crown className="w-3 h-3 mr-1" />
              Premium
            </Badge>
          )}
        </div>
        
        {/* XP Balance for cosmetics */}
        <div className="mt-3 p-3 bg-purple/10 rounded-lg border border-purple/20 flex items-center justify-between">
          <span className="text-sm text-muted-foreground">Available XP</span>
          <span className="font-mono text-lg text-purple flex items-center gap-1">
            <Sparkles className="w-4 h-4" />
            {state.xp.toFixed(0)}
          </span>
        </div>
      </CardHeader>
      
      <CardContent>
        <Tabs defaultValue="premium" className="w-full">
          <TabsList className="grid w-full grid-cols-2 mb-4">
            <TabsTrigger value="premium" className="gap-2">
              <Crown className="w-4 h-4" />
              Premium
            </TabsTrigger>
            <TabsTrigger value="cosmetics" className="gap-2">
              <Palette className="w-4 h-4" />
              Cosmetics
            </TabsTrigger>
          </TabsList>
          
          {/* Premium Items Tab */}
          <TabsContent value="premium">
            <ScrollArea className="h-[400px] pr-4">
              <div className="space-y-3">
                {/* Premium Subscription Highlight */}
                <div className="p-4 rounded-xl bg-gradient-to-br from-gold/20 via-purple/10 to-gold/20 border-2 border-gold/30 relative overflow-hidden">
                  <div className="absolute top-0 right-0 w-20 h-20 bg-gold/10 rounded-full blur-2xl" />
                  
                  <Badge className="premium-badge mb-2">
                    <Star className="w-3 h-3 mr-1" />
                    Best Value
                  </Badge>
                  
                  <h3 className="font-display text-xl mb-1">Premium Subscription</h3>
                  <p className="text-sm text-muted-foreground mb-3">All benefits + exclusive items</p>
                  
                  <ul className="text-xs text-muted-foreground space-y-1 mb-4">
                    <li className="flex items-center gap-2">
                      <Check className="w-3 h-3 text-success" /> 2x Money Multiplier
                    </li>
                    <li className="flex items-center gap-2">
                      <Check className="w-3 h-3 text-success" /> 1.5x XP Boost
                    </li>
                    <li className="flex items-center gap-2">
                      <Check className="w-3 h-3 text-success" /> Auction Hints
                    </li>
                    <li className="flex items-center gap-2">
                      <Check className="w-3 h-3 text-success" /> Auto-Bid System
                    </li>
                    <li className="flex items-center gap-2">
                      <Check className="w-3 h-3 text-success" /> Exclusive Auctions
                    </li>
                    <li className="flex items-center gap-2">
                      <Check className="w-3 h-3 text-success" /> No Ads
                    </li>
                  </ul>
                  
                  <div className="flex items-center justify-between">
                    <div>
                      <span className="text-2xl font-bold gradient-text-gold">$5.99</span>
                      <span className="text-xs text-muted-foreground">/month</span>
                    </div>
                    <Button 
                      variant="premium"
                      onClick={() => handlePurchasePremium(PREMIUM_ITEMS.find(p => p.id === 'pm5'))}
                      disabled={state.purchasedPremiumItems.includes('pm5')}
                    >
                      {state.purchasedPremiumItems.includes('pm5') ? (
                        <>
                          <Check className="w-4 h-4 mr-1" /> Active
                        </>
                      ) : (
                        'Subscribe'
                      )}
                    </Button>
                  </div>
                </div>
                
                {/* Individual Items */}
                {PREMIUM_ITEMS.filter(item => item.id !== 'pm5').map(item => {
                  const isPurchased = state.purchasedPremiumItems.includes(item.id);
                  
                  return (
                    <div 
                      key={item.id}
                      className={`
                        p-4 rounded-lg border transition-all
                        ${isPurchased 
                          ? 'bg-success/10 border-success/30' 
                          : 'bg-muted/30 border-border/50 hover:border-gold/30'
                        }
                      `}
                    >
                      <div className="flex items-start justify-between">
                        <div className="flex-1">
                          <h4 className="font-semibold">{item.name}</h4>
                          <p className="text-xs text-muted-foreground mb-2">{item.description}</p>
                          <span className="text-lg font-bold text-gold">${item.price}</span>
                        </div>
                        <Button
                          variant={isPurchased ? "outline" : "gold"}
                          size="sm"
                          onClick={() => handlePurchasePremium(item)}
                          disabled={isPurchased}
                        >
                          {isPurchased ? (
                            <>
                              <Check className="w-4 h-4 mr-1" /> Owned
                            </>
                          ) : (
                            'Buy'
                          )}
                        </Button>
                      </div>
                    </div>
                  );
                })}
              </div>
            </ScrollArea>
          </TabsContent>
          
          {/* Cosmetics Tab */}
          <TabsContent value="cosmetics">
            <ScrollArea className="h-[400px] pr-4">
              <div className="space-y-3">
                <p className="text-xs text-muted-foreground text-center mb-4">
                  Spend XP to unlock cosmetic decorations for your virtual mansion!
                </p>
                
                {COSMETIC_ITEMS.map(cosmetic => {
                  const isOwned = state.ownedCosmetics.includes(cosmetic.id);
                  const canAfford = state.xp >= cosmetic.xpCost;
                  
                  return (
                    <div 
                      key={cosmetic.id}
                      className={`
                        p-4 rounded-lg border transition-all
                        ${isOwned 
                          ? 'bg-purple/10 border-purple/30' 
                          : 'bg-muted/30 border-border/50 hover:border-purple/30'
                        }
                      `}
                    >
                      <div className="flex items-center gap-3">
                        <div className={`
                          w-12 h-12 rounded-lg flex items-center justify-center text-2xl
                          ${isOwned ? 'bg-purple/20' : 'bg-muted/50'}
                        `}>
                          {cosmetic.icon}
                        </div>
                        
                        <div className="flex-1">
                          <h4 className="font-semibold text-sm">{cosmetic.name}</h4>
                          <p className="text-xs text-muted-foreground">{cosmetic.description}</p>
                          <Badge variant="outline" className="text-xs mt-1">
                            {cosmetic.category}
                          </Badge>
                        </div>
                        
                        <div className="text-right">
                          <div className="flex items-center gap-1 text-purple mb-2">
                            <Sparkles className="w-3 h-3" />
                            <span className="font-mono">{cosmetic.xpCost}</span>
                          </div>
                          <Button
                            variant={isOwned ? "outline" : canAfford ? "purple" : "outline"}
                            size="sm"
                            onClick={() => handleBuyCosmetic(cosmetic)}
                            disabled={isOwned || !canAfford}
                          >
                            {isOwned ? (
                              <>
                                <Check className="w-3 h-3 mr-1" /> Owned
                              </>
                            ) : (
                              'Unlock'
                            )}
                          </Button>
                        </div>
                      </div>
                    </div>
                  );
                })}
              </div>
            </ScrollArea>
          </TabsContent>
        </Tabs>
        
        {/* Disclaimer */}
        <p className="text-xs text-muted-foreground text-center mt-4">
          * Premium purchases are mock/simulated for demo purposes
        </p>
      </CardContent>
    </Card>
  );
};

export default PremiumStoreSection;
