import React, { useState } from 'react';
import { useGame } from '../../context/GameContext';
import { Card, CardContent, CardHeader, CardTitle } from '../ui/card';
import { Button } from '../ui/button';
import { Badge } from '../ui/badge';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '../ui/tabs';
import { ScrollArea } from '../ui/scroll-area';
import { formatMoney, STOCKS, CRYPTOS } from '../../lib/gameData';
import { TrendingUp, TrendingDown, BarChart3, Bitcoin, ArrowUpRight, ArrowDownRight } from 'lucide-react';

export const InvestmentSection = () => {
  const { state, actions } = useGame();
  const [buyAmount, setBuyAmount] = useState({});
  
  const calculatePortfolioValue = () => {
    let stockValue = 0;
    let cryptoValue = 0;
    
    Object.entries(state.stockPortfolio).forEach(([id, holding]) => {
      if (holding && state.stockPrices[id]) {
        stockValue += holding.shares * state.stockPrices[id];
      }
    });
    
    Object.entries(state.cryptoPortfolio).forEach(([id, holding]) => {
      if (holding && state.cryptoPrices[id]) {
        cryptoValue += holding.amount * state.cryptoPrices[id];
      }
    });
    
    return { stockValue, cryptoValue, total: stockValue + cryptoValue };
  };
  
  const portfolio = calculatePortfolioValue();
  
  const StockCard = ({ stock }) => {
    const currentPrice = state.stockPrices[stock.id] || stock.basePrice;
    const holding = state.stockPortfolio[stock.id];
    const priceChange = ((currentPrice - stock.basePrice) / stock.basePrice) * 100;
    const isUp = priceChange >= 0;
    const shares = buyAmount[stock.id] || 1;
    
    return (
      <div className="p-4 bg-muted/30 rounded-lg border border-border/50 hover:border-gold/30 transition-colors">
        <div className="flex items-start justify-between mb-3">
          <div className="flex items-center gap-2">
            <span className="text-2xl">{stock.icon}</span>
            <div>
              <h4 className="font-semibold">{stock.id}</h4>
              <p className="text-xs text-muted-foreground">{stock.name}</p>
            </div>
          </div>
          <div className={`flex items-center gap-1 text-sm ${isUp ? 'stock-up' : 'stock-down'}`}>
            {isUp ? <ArrowUpRight className="w-4 h-4" /> : <ArrowDownRight className="w-4 h-4" />}
            {priceChange.toFixed(2)}%
          </div>
        </div>
        
        <div className="flex items-baseline gap-2 mb-3">
          <span className="font-mono text-xl">{formatMoney(currentPrice)}</span>
          <span className="text-xs text-muted-foreground">per share</span>
        </div>
        
        {holding && (
          <div className="mb-3 p-2 bg-card/50 rounded text-sm">
            <span className="text-muted-foreground">Owned: </span>
            <span className="font-mono text-foreground">{holding.shares} shares</span>
            <span className="text-muted-foreground"> = </span>
            <span className="font-mono text-gold">{formatMoney(holding.shares * currentPrice)}</span>
          </div>
        )}
        
        <div className="flex items-center gap-2">
          <input
            type="number"
            min="1"
            value={shares}
            onChange={(e) => setBuyAmount({ ...buyAmount, [stock.id]: Math.max(1, parseInt(e.target.value) || 1) })}
            className="w-16 px-2 py-1 bg-input border border-border rounded text-center text-sm"
          />
          <Button 
            variant="outline" 
            size="sm"
            onClick={() => actions.buyStock(stock.id, shares)}
            disabled={state.money < currentPrice * shares}
            className="flex-1"
          >
            Buy
          </Button>
          <Button 
            variant="ghost" 
            size="sm"
            onClick={() => actions.sellStock(stock.id, shares)}
            disabled={!holding || holding.shares < shares}
            className="flex-1"
          >
            Sell
          </Button>
        </div>
      </div>
    );
  };
  
  const CryptoCard = ({ crypto }) => {
    const currentPrice = state.cryptoPrices[crypto.id] || crypto.basePrice;
    const holding = state.cryptoPortfolio[crypto.id];
    const priceChange = ((currentPrice - crypto.basePrice) / crypto.basePrice) * 100;
    const isUp = priceChange >= 0;
    const amount = buyAmount[`crypto_${crypto.id}`] || 1;
    
    return (
      <div className="p-4 bg-muted/30 rounded-lg border border-border/50 hover:border-purple/30 transition-colors">
        <div className="flex items-start justify-between mb-3">
          <div className="flex items-center gap-2">
            <span className="text-2xl">{crypto.icon}</span>
            <div>
              <h4 className="font-semibold">{crypto.id}</h4>
              <p className="text-xs text-muted-foreground">{crypto.name}</p>
            </div>
          </div>
          <div className="text-right">
            <Badge variant="outline" className="text-xs mb-1">
              {(crypto.volatility * 100).toFixed(0)}% volatile
            </Badge>
            <div className={`flex items-center gap-1 text-sm justify-end ${isUp ? 'stock-up' : 'stock-down'}`}>
              {isUp ? <ArrowUpRight className="w-4 h-4" /> : <ArrowDownRight className="w-4 h-4" />}
              {priceChange.toFixed(2)}%
            </div>
          </div>
        </div>
        
        <div className="flex items-baseline gap-2 mb-3">
          <span className="font-mono text-xl">{formatMoney(currentPrice)}</span>
          <span className="text-xs text-muted-foreground">per coin</span>
        </div>
        
        {holding && (
          <div className="mb-3 p-2 bg-card/50 rounded text-sm">
            <span className="text-muted-foreground">Owned: </span>
            <span className="font-mono text-foreground">{holding.amount.toFixed(4)} {crypto.id}</span>
            <span className="text-muted-foreground"> = </span>
            <span className="font-mono text-purple">{formatMoney(holding.amount * currentPrice)}</span>
          </div>
        )}
        
        <div className="flex items-center gap-2">
          <input
            type="number"
            min="0.001"
            step="0.001"
            value={amount}
            onChange={(e) => setBuyAmount({ ...buyAmount, [`crypto_${crypto.id}`]: Math.max(0.001, parseFloat(e.target.value) || 0.001) })}
            className="w-20 px-2 py-1 bg-input border border-border rounded text-center text-sm"
          />
          <Button 
            variant="outline" 
            size="sm"
            onClick={() => actions.buyCrypto(crypto.id, amount)}
            disabled={state.money < currentPrice * amount}
            className="flex-1"
          >
            Buy
          </Button>
          <Button 
            variant="ghost" 
            size="sm"
            onClick={() => actions.sellCrypto(crypto.id, amount)}
            disabled={!holding || holding.amount < amount}
            className="flex-1"
          >
            Sell
          </Button>
        </div>
      </div>
    );
  };
  
  return (
    <Card variant="glass" className="relative overflow-hidden">
      <div className="absolute inset-0 bg-gradient-to-br from-blue-500/5 via-transparent to-purple/5 pointer-events-none" />
      
      <CardHeader className="pb-3">
        <div className="flex items-center justify-between">
          <CardTitle className="flex items-center gap-2 text-xl">
            <BarChart3 className="w-5 h-5 text-gold" />
            Investment Corner
          </CardTitle>
        </div>
        
        {/* Portfolio Summary */}
        <div className="grid grid-cols-3 gap-2 mt-4">
          <div className="p-2 bg-muted/30 rounded-lg text-center">
            <p className="text-xs text-muted-foreground">Stocks</p>
            <p className="font-mono text-sm text-foreground">{formatMoney(portfolio.stockValue)}</p>
          </div>
          <div className="p-2 bg-muted/30 rounded-lg text-center">
            <p className="text-xs text-muted-foreground">Crypto</p>
            <p className="font-mono text-sm text-purple">{formatMoney(portfolio.cryptoValue)}</p>
          </div>
          <div className="p-2 bg-muted/30 rounded-lg text-center">
            <p className="text-xs text-muted-foreground">Total</p>
            <p className="font-mono text-sm text-gold">{formatMoney(portfolio.total)}</p>
          </div>
        </div>
        
        {/* Profit/Loss */}
        <div className={`mt-2 p-2 rounded-lg text-center text-sm ${state.investmentProfit >= 0 ? 'bg-success/20 text-success' : 'bg-destructive/20 text-destructive'}`}>
          {state.investmentProfit >= 0 ? <TrendingUp className="w-4 h-4 inline mr-1" /> : <TrendingDown className="w-4 h-4 inline mr-1" />}
          Total P/L: {formatMoney(state.investmentProfit)}
        </div>
      </CardHeader>
      
      <CardContent>
        <Tabs defaultValue="stocks" className="w-full">
          <TabsList className="grid w-full grid-cols-2 mb-4">
            <TabsTrigger value="stocks" className="gap-2">
              <BarChart3 className="w-4 h-4" />
              Stocks
            </TabsTrigger>
            <TabsTrigger value="crypto" className="gap-2">
              <Bitcoin className="w-4 h-4" />
              Crypto
            </TabsTrigger>
          </TabsList>
          
          <TabsContent value="stocks">
            <ScrollArea className="h-[400px] pr-4">
              <div className="space-y-3">
                {STOCKS.map(stock => (
                  <StockCard key={stock.id} stock={stock} />
                ))}
              </div>
            </ScrollArea>
          </TabsContent>
          
          <TabsContent value="crypto">
            <ScrollArea className="h-[400px] pr-4">
              <div className="space-y-3">
                {CRYPTOS.map(crypto => (
                  <CryptoCard key={crypto.id} crypto={crypto} />
                ))}
              </div>
            </ScrollArea>
          </TabsContent>
        </Tabs>
      </CardContent>
    </Card>
  );
};

export default InvestmentSection;
