import React from 'react';
import { useGame } from '../../context/GameContext';
import { Card, CardContent, CardHeader, CardTitle } from '../ui/card';
import { Badge } from '../ui/badge';
import { ScrollArea } from '../ui/scroll-area';
import { formatMoney } from '../../lib/gameData';
import { Users, Trophy, Crown, Sparkles, TrendingUp, Package } from 'lucide-react';

// Mock leaderboard data - in real app this would come from backend
const generateMockLeaderboard = (playerStats) => {
  const mockPlayers = [
    { name: 'CryptoKing99', money: 987654321, xp: 15000, rareItems: 45 },
    { name: 'TycoonMaster', money: 876543210, xp: 12500, rareItems: 38 },
    { name: 'GoldRush2024', money: 765432109, xp: 11000, rareItems: 32 },
    { name: 'InvestorPro', money: 654321098, xp: 9500, rareItems: 28 },
    { name: 'LuckyBidder', money: 543210987, xp: 8000, rareItems: 25 },
    { name: 'StockSavant', money: 432109876, xp: 7000, rareItems: 22 },
    { name: 'AuctionAce', money: 321098765, xp: 6000, rareItems: 18 },
    { name: 'ClickChamp', money: 210987654, xp: 5000, rareItems: 15 },
    { name: 'WealthWizard', money: 109876543, xp: 4000, rareItems: 12 },
    { name: 'BizBuilder', money: 98765432, xp: 3500, rareItems: 10 },
  ];
  
  // Insert player at appropriate position
  const player = { 
    name: 'You', 
    money: playerStats.money, 
    xp: playerStats.xp, 
    rareItems: playerStats.rareItems,
    isPlayer: true 
  };
  
  const allPlayers = [...mockPlayers, player];
  
  return {
    byMoney: [...allPlayers].sort((a, b) => b.money - a.money),
    byXp: [...allPlayers].sort((a, b) => b.xp - a.xp),
    byRareItems: [...allPlayers].sort((a, b) => b.rareItems - a.rareItems)
  };
};

export const LeaderboardSection = () => {
  const { state } = useGame();
  const [activeTab, setActiveTab] = React.useState('money');
  
  const leaderboards = generateMockLeaderboard({
    money: state.totalMoneyEarned,
    xp: state.totalXpEarned,
    rareItems: state.rareItemsWon + state.epicItemsWon + state.mythicItemsWon
  });
  
  const getLeaderboardData = () => {
    switch (activeTab) {
      case 'money': return leaderboards.byMoney;
      case 'xp': return leaderboards.byXp;
      case 'rareItems': return leaderboards.byRareItems;
      default: return leaderboards.byMoney;
    }
  };
  
  const getValueDisplay = (player) => {
    switch (activeTab) {
      case 'money': return formatMoney(player.money);
      case 'xp': return `${player.xp.toLocaleString()} XP`;
      case 'rareItems': return `${player.rareItems} items`;
      default: return '';
    }
  };
  
  const getRankBadge = (index) => {
    if (index === 0) return <Crown className="w-5 h-5 text-gold" />;
    if (index === 1) return <span className="text-lg"></span>;
    if (index === 2) return <span className="text-lg"></span>;
    return <span className="text-sm text-muted-foreground">#{index + 1}</span>;
  };
  
  const data = getLeaderboardData();
  const playerRank = data.findIndex(p => p.isPlayer) + 1;
  
  return (
    <Card variant="glass" className="relative overflow-hidden">
      <div className="absolute inset-0 bg-gradient-to-br from-gold/5 via-transparent to-purple/5 pointer-events-none" />
      
      <CardHeader className="pb-3">
        <CardTitle className="flex items-center gap-2 text-xl">
          <Users className="w-5 h-5 text-gold" />
          Leaderboards
        </CardTitle>
        
        {/* Player Rank Summary */}
        <div className="mt-3 p-3 bg-gold/10 rounded-lg border border-gold/20">
          <div className="flex items-center justify-between">
            <span className="text-sm text-muted-foreground">Your Rank</span>
            <div className="flex items-center gap-2">
              <Trophy className="w-4 h-4 text-gold" />
              <span className="font-mono text-lg text-gold">#{playerRank}</span>
            </div>
          </div>
        </div>
      </CardHeader>
      
      <CardContent>
        {/* Tab Selection */}
        <div className="flex gap-2 mb-4">
          <button
            onClick={() => setActiveTab('money')}
            className={`flex-1 p-2 rounded-lg text-sm transition-all flex items-center justify-center gap-2 ${
              activeTab === 'money' 
                ? 'bg-gold/20 text-gold border border-gold/30' 
                : 'bg-muted/30 text-muted-foreground border border-transparent hover:border-border'
            }`}
          >
            <TrendingUp className="w-4 h-4" />
            Money
          </button>
          <button
            onClick={() => setActiveTab('xp')}
            className={`flex-1 p-2 rounded-lg text-sm transition-all flex items-center justify-center gap-2 ${
              activeTab === 'xp' 
                ? 'bg-purple/20 text-purple border border-purple/30' 
                : 'bg-muted/30 text-muted-foreground border border-transparent hover:border-border'
            }`}
          >
            <Sparkles className="w-4 h-4" />
            XP
          </button>
          <button
            onClick={() => setActiveTab('rareItems')}
            className={`flex-1 p-2 rounded-lg text-sm transition-all flex items-center justify-center gap-2 ${
              activeTab === 'rareItems' 
                ? 'bg-rarity-rare/20 text-rarity-rare border border-rarity-rare/30' 
                : 'bg-muted/30 text-muted-foreground border border-transparent hover:border-border'
            }`}
          >
            <Package className="w-4 h-4" />
            Rare Items
          </button>
        </div>
        
        {/* Leaderboard List */}
        <ScrollArea className="h-[400px] pr-4">
          <div className="space-y-2">
            {data.map((player, index) => (
              <div 
                key={`${player.name}-${index}`}
                className={`
                  p-3 rounded-lg border transition-all flex items-center gap-3
                  ${player.isPlayer 
                    ? 'bg-gold/10 border-gold/30 ring-1 ring-gold/20' 
                    : index < 3 
                      ? 'bg-muted/30 border-gold/20' 
                      : 'bg-muted/20 border-border/50'
                  }
                `}
              >
                {/* Rank */}
                <div className="w-8 flex items-center justify-center">
                  {getRankBadge(index)}
                </div>
                
                {/* Player Info */}
                <div className="flex-1 min-w-0">
                  <p className={`font-semibold truncate ${player.isPlayer ? 'text-gold' : ''}`}>
                    {player.name}
                    {player.isPlayer && (
                      <Badge className="ml-2 text-xs bg-gold/20 text-gold border-0">
                        You
                      </Badge>
                    )}
                  </p>
                </div>
                
                {/* Value */}
                <div className="text-right">
                  <p className={`font-mono ${
                    activeTab === 'money' ? 'text-gold' : 
                    activeTab === 'xp' ? 'text-purple' : 
                    'text-rarity-rare'
                  }`}>
                    {getValueDisplay(player)}
                  </p>
                </div>
              </div>
            ))}
          </div>
        </ScrollArea>
        
        {/* Share Section */}
        <div className="mt-4 p-3 bg-muted/30 rounded-lg text-center">
          <p className="text-xs text-muted-foreground mb-2">Share your achievements!</p>
          <div className="flex justify-center gap-2">
            <Badge variant="outline" className="cursor-pointer hover:bg-muted/50 transition-colors">
               Discord
            </Badge>
            <Badge variant="outline" className="cursor-pointer hover:bg-muted/50 transition-colors">
               TikTok
            </Badge>
            <Badge variant="outline" className="cursor-pointer hover:bg-muted/50 transition-colors">
               Reddit
            </Badge>
          </div>
        </div>
      </CardContent>
    </Card>
  );
};

export default LeaderboardSection;
