import React from 'react';
import { useGame } from '../../context/GameContext';
import { Card, CardContent, CardHeader, CardTitle } from '../ui/card';
import { Badge } from '../ui/badge';
import { ScrollArea } from '../ui/scroll-area';
import { RARITY_CONFIG } from '../../lib/gameData';
import { Package, Sparkles, Filter } from 'lucide-react';

export const InventorySection = () => {
  const { state } = useGame();
  const [filterRarity, setFilterRarity] = React.useState('all');
  
  const filteredInventory = filterRarity === 'all' 
    ? state.inventory 
    : state.inventory.filter(item => item.rarity === filterRarity);
  
  const getRarityCount = (rarity) => {
    return state.inventory.filter(item => item.rarity === rarity).length;
  };
  
  return (
    <Card variant="glass" className="relative overflow-hidden">
      <div className="absolute inset-0 bg-gradient-to-br from-gold/5 via-transparent to-purple/5 pointer-events-none" />
      
      <CardHeader className="pb-3">
        <div className="flex items-center justify-between">
          <CardTitle className="flex items-center gap-2 text-xl">
            <Package className="w-5 h-5 text-gold" />
            Collection
          </CardTitle>
          <Badge variant="outline" className="text-muted-foreground">
            {state.inventory.length} Items
          </Badge>
        </div>
        
        {/* Rarity Summary */}
        <div className="flex flex-wrap gap-2 mt-3">
          <button
            onClick={() => setFilterRarity('all')}
            className={`px-3 py-1 rounded-full text-xs transition-all ${
              filterRarity === 'all' 
                ? 'bg-gold/20 text-gold border border-gold/30' 
                : 'bg-muted/30 text-muted-foreground border border-transparent hover:border-border'
            }`}
          >
            All ({state.inventory.length})
          </button>
          {Object.entries(RARITY_CONFIG).map(([key, config]) => (
            <button
              key={key}
              onClick={() => setFilterRarity(key)}
              className={`px-3 py-1 rounded-full text-xs transition-all ${
                filterRarity === key 
                  ? `bg-${config.color}/20 ${config.textColor} border border-current` 
                  : 'bg-muted/30 text-muted-foreground border border-transparent hover:border-border'
              }`}
              style={{
                borderColor: filterRarity === key ? `hsl(var(--${config.color}))` : undefined,
                color: filterRarity === key ? `hsl(var(--${config.color}))` : undefined
              }}
            >
              {config.name} ({getRarityCount(key)})
            </button>
          ))}
        </div>
      </CardHeader>
      
      <CardContent>
        <ScrollArea className="h-[400px] pr-4">
          {filteredInventory.length === 0 ? (
            <div className="text-center py-12">
              <Package className="w-12 h-12 mx-auto mb-4 text-muted-foreground" />
              <p className="text-muted-foreground">
                {filterRarity === 'all' 
                  ? 'No items yet. Win auctions to collect items!'
                  : `No ${RARITY_CONFIG[filterRarity]?.name} items yet.`
                }
              </p>
            </div>
          ) : (
            <div className="grid grid-cols-2 md:grid-cols-3 gap-3">
              {filteredInventory.map((item, index) => {
                const rarityConfig = RARITY_CONFIG[item.rarity];
                
                return (
                  <div 
                    key={`${item.id}-${index}`}
                    className={`
                      relative rounded-lg overflow-hidden border-2 card-hover
                      ${rarityConfig?.borderClass}
                    `}
                  >
                    {/* Image */}
                    <div className="aspect-square relative">
                      <img 
                        src={item.image} 
                        alt={item.name}
                        className="w-full h-full object-cover"
                      />
                      {item.rarity === 'mythic' && (
                        <div className="absolute inset-0 bg-gradient-to-t from-gold/40 to-transparent animate-pulse" />
                      )}
                      
                      {/* Rarity Badge */}
                      <Badge 
                        className={`
                          absolute top-2 left-2 text-xs
                          ${rarityConfig?.bgColor}/80 ${rarityConfig?.textColor}
                        `}
                        style={{
                          backgroundColor: `hsl(var(--${rarityConfig?.color}) / 0.8)`,
                          color: item.rarity === 'mythic' ? 'black' : 'white'
                        }}
                      >
                        {rarityConfig?.name}
                      </Badge>
                    </div>
                    
                    {/* Info */}
                    <div className="p-2 bg-card/80">
                      <h4 className="font-semibold text-sm truncate">{item.name}</h4>
                      <div className="flex items-center gap-1 text-xs text-muted-foreground">
                        <Sparkles className="w-3 h-3 text-purple" />
                        +{item.xpReward} XP
                      </div>
                    </div>
                  </div>
                );
              })}
            </div>
          )}
        </ScrollArea>
      </CardContent>
    </Card>
  );
};

export default InventorySection;
